          'ÊöñÊöñ', 'Ê≥°Ê≥°', 'Á≥ØÁ≥Ø', 'Ëø∑Ëå´', 'ÂøßÈÉÅ'
        ],
        nouns: [
          'Ë•øËìùËä±', 'È≤∑È±ºÁÉß', 'ËçâËéìÊ¥æ', 'ÁÉ≠ÂèØÂèØ',
          'Â•áÂºÇÊûú', 'Â∞èÈ∫ªËñØ', 'Â∞èÂèØÈ¢Ç', 'Â∞èÊõ≤Â•á', 'ÊòüÂÜ∞‰πê',
          'Ëè†ËêùÂåÖ', 'Â∞è‰∫ëÂêû', 'Á≥ØÁ±≥Âõ¢', 'Â∞èÊ≥°Ëäô', 'ÊéíÈ™®Âπ¥Á≥ï'
        ]
      },
      {
        // È£éÊ†º2: Ê†°Âõ≠/Âú∞Âêç
        adjectives: [
          'ËôπÊ¢ÖÂçóË∑Ø', '‰∏≠Â±±ÂåóË∑Ø', 'ÂâëÂ∑ùË∑Ø‰∏ä', 'ÈáëÊ≤ôÊ±üË∑Ø', '‰∏úÂ∑ùË∑ØÁöÑ',
          'Ëé≤Ëä±ÂçóË∑Ø', 'ËÄÅÂ≠êÊÄùÁª¥', 'ÈáèÂ≠êÊÄùÁª¥', 'Ê±ÇÂÆûÂàõÈÄ†', '‰∏∫‰∫∫Â∏àË°®',
          '‰∏ΩÂ®ÉÊ≤≥Áïî', 'Ê®±Ê°ÉÊ≤≥Áïî'
        ],
        nouns: [
          'ÁßãÂÆûÈòÅ', 'Â§èÈõ®ÂéÖ', 'ÂÜ¨ÊúàÂéÖ', 'ÂÜ¨Êó•ÂéÖ', 'Êª°Â§©Êòü',
          'ÁéØÁêÉÊ∏Ø', 'ËãèÂ∑ûÊ≤≥', 'ÈªÑÊµ¶Ê±ü'
        ]
      },
      {
        // È£éÊ†º3: ÁßëÊäÄ/Â≠¶ÊúØ
        adjectives: [
          'ËΩÆÊç¢', 'ÂºÇÊ≠•', 'Ê≠£‰∫§', 'ÂÖ®ÊÅØ', 'ÂÜíÊ≥°', 'ÂºÇÊûÑ', 'Ê≠£Âàô', 'ËµõÂçö',
          'Á±ªËÑë', '‰∫íÊòì', 'ËÑâÂÜ≤', 'Áü©Èòµ', 'ÂØºÁîµ'
        ],
        nouns: [
          'ÈáèÂ≠êÊÄùÁª¥', 'ËÄÅÂ≠êÊÄùÁª¥', 'Â§öÈ°πÂºè', 'ÈÄªËæëÈó®', 'CMOS', 'TTL', '‰∫åÂèâÊ†ë',
          'Ë∂ÖÂØº‰Ωì', 'ÂÖâÂ≠êÊùü', 'ÁÆóÂäõÊ†∏', 'Ê®°ÂùóÊú∫', 'Êô∫ËÉΩÁ´Ø', 'ÈÄÜÁü©Èòµ',
          'ÂØπÁß∞ÈòµÂ≠ê', 'ÂÖ´Êú®Â§©Á∫ø', 'ÁâõÈ°øÁéØ', 'Â∏åÂ∞î‰ºØÁâπÁ©∫Èó¥', 'pnÁªì', 'Âπ≤Ê∂â‰ª™', 'Á§∫Ê≥¢Âô®'
        ]
      },
      {
        // È£éÊ†º4: ÊñáËâ∫/ÊäΩË±°
        adjectives: [
          'ÊöÆËâ≤', 'ÊòüÊæú', 'È£éÊ†ñ', '‰∫ë‰∏ä', 'ÂæÆÂÖâ', 'ÁßãËâ≤', 'ÊúàÁôΩ', 'ÈÅ•Ëøú', 'Ê∑°Â¢®', 'ÊûùÈó¥',
          'Â±±Êµ∑', 'ÈõæËµ∑', 'Â≤ÅÊúà', 'ÈùíËó§', 'ËΩªËàü', 'Ê∏©Êüî', 'Êô¥Á©∫', 'ÊµÅÂÖâ', 'Èõ®ËêΩ', 'Ê∏ÖÊô®'
        ],
        nouns: [
          'Â∞èÂøÉÂøÉ', 'Â∞èÊ≥°Ê≥°', 'Â∞èÈóÆÂè∑', 'Â∞èÂèπÂè∑', 'Â∞èÊñπÂùó', 'ÊöñÁ≤íÂ≠ê', 'Â∞èÁÅµÊÑü', 'Â∞èËÆ∞ÂøÜ', 'Â∞èËÉΩÈáè',
          'Â∞èÈü≥Á¨¶', 'ËΩªÊÉÖÁª™', 'Â∞èÂ∏åÊúõ', 'Â∞èÊ¢¶Â¢É', 'Â∞èËÆ°Âàí', 'Â∞èÊïÖ‰∫ã', 'Â∞èÂøµÂ§¥', 'Â∞èÂÆáÂÆô', 'Â∞èÊúüÂæÖ'
        ]
      }
    ]
    
    return {
      editForm: {
        nickname: '',
        avatarUrl: '',
        user_sex: '',
        major: '', 
        user_introduce: ''
      },
      hasChanges: false,
	    enableWatch: false,
      presetAvatars: presetAvatarList,
      nameStyles: nameStyles,
      // üî• NEW: Ê∑ªÂä†È£éÊ†ºÁ¥¢ÂºïËÆ°Êï∞Âô®
      currentStyleIndex: 0
    }
  },
  
  computed: {
    ...mapState('m_user', ['userBase', 'openid', 'userMajor'])
  },
  
  onLoad() {
    this.initEditForm()
	    this.$nextTick(() => {
	      this.enableWatch = true
	    })
  },
  
  // ÁõëÂê¨Ë°®ÂçïÂèòÂåñ (Êó†‰øÆÊîπ)
  watch: {
    editForm: {
      handler() {
               if (this.enableWatch) {
                 this.hasChanges = true
               }
      },
      deep: true
    }
  },
  
  // ËøîÂõûÂâçÊèêÁ§∫ (Êó†‰øÆÊîπ)
  onBackPress() {
    if (this.hasChanges) {
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'ÊÇ®ÊúâÊú™‰øùÂ≠òÁöÑ‰øÆÊîπÔºåÁ°ÆÂÆöË¶ÅÁ¶ªÂºÄÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            uni.navigateBack()
          }
        }
      })
      return true
    }
  },
  
  methods: {
    ...mapMutations('m_user', ['updateUserBase']),
    
    // üî• MODIFIED: ÂàùÂßãÂåñË°®Âçï
    initEditForm() {
      this.editForm = {
        nickname: this.userBase.nickname || '',
        avatarUrl: this.userBase.avatarUrl || '',
        user_sex: this.userBase.user_sex || '‰øùÂØÜ',
        major: this.userBase.major || '',
        user_introduce: this.userBase.user_introduce || ''
      }
      
      if (!this.editForm.nickname) {
        // üî• MODIFIED: ÂàùÂßãÂåñÊó∂ÁîüÊàêÊòµÁß∞Ôºå‰ΩÜ‰∏çÊé®ËøõËÆ°Êï∞Âô®
        this.generateRandomNickname(false) 
      }
      
      this.hasChanges = false
	  this.enableWatch = false
    },
    
    // üî• MODIFIED: ÁîüÊàêÈöèÊú∫ÊòµÁß∞ÁöÑÈÄªËæë
    /**
     * @param {boolean} incrementStyle - ÊòØÂê¶Êé®ËøõÈ£éÊ†ºËÆ°Êï∞Âô®ÔºàÁî®Êà∑ÁÇπÂáªÊó∂‰∏∫trueÔºåÂàùÂßãÂåñÊó∂‰∏∫falseÔºâ
     */
    generateRandomNickname(incrementStyle = false) {
      
      // 1. ÂÜ≥ÂÆö‰ΩøÁî®Âì™‰∏™Á¥¢Âºï
      let styleIndexToUse = this.currentStyleIndex;
      
      // 2. Â¶ÇÊûúÊòØÁî®Êà∑ÁÇπÂáª‚ÄúÊç¢‰∏Ä‰∏™‚ÄùÔºåÂàôÊé®ËøõÁ¥¢Âºï
      if (incrementStyle) {
        // Êé®ËøõÁ¥¢ÂºïÔºåÂπ∂‰ΩøÁî® % ËøêÁÆóÁ¨¶Á°Æ‰øùÂæ™ÁéØ
        this.currentStyleIndex = (this.currentStyleIndex + 1) % this.nameStyles.length;
        styleIndexToUse = this.currentStyleIndex;
      }
      
      const selectedStyle = this.nameStyles[styleIndexToUse];
      
      let newName = '';
      let attempts = 0; // ÂÆâÂÖ®ÈîÅÔºåÈò≤Ê≠¢Ê≠ªÂæ™ÁéØ
      
      // 3. Âæ™ÁéØÁõ¥Âà∞ÁîüÊàê‰∏Ä‰∏™‰∏çÈáçÂ§çÁöÑÊòµÁß∞
      do {
        const adjIndex = Math.floor(Math.random() * selectedStyle.adjectives.length);
        const adj = selectedStyle.adjectives[adjIndex];
        
        const nounIndex = Math.floor(Math.random() * selectedStyle.nouns.length);
        const noun = selectedStyle.nouns[nounIndex];
        
        newName = (adj + noun).substring(0, 20);
        attempts++;
      } while (
        newName === this.editForm.nickname && // ÈÅøÂÖçÂíåÂΩìÂâçÊòµÁß∞ÈáçÂ§ç
        (selectedStyle.adjectives.length > 1 || selectedStyle.nouns.length > 1) && // Á°Æ‰øùËØçÂ∫ìÊúâË∂≥Â§üÂ§öÁöÑËØç
        attempts < 10 // ÊúÄÂ§öÂ∞ùËØï10Ê¨°
      );

      // 4. ËµãÂÄº
      this.editForm.nickname = newName;
    },
    
    // ËøîÂõû (Êó†‰øÆÊîπ)
    goBack() {
      if (this.hasChanges) {
        uni.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÊÇ®ÊúâÊú™‰øùÂ≠òÁöÑ‰øÆÊîπÔºåÁ°ÆÂÆöË¶ÅÁ¶ªÂºÄÂêóÔºü',
          success: (res) => {
            if (res.confirm) {
              uni.navigateBack()
            }
          }
        })
      } else {
        uni.navigateBack()
      }
    },
    
    // ‰øùÂ≠òËµÑÊñô (Êó†‰øÆÊîπ)
    async saveProfile() {
      if (!this.editForm.nickname.trim()) {
        return uni.showToast({ 
          title: 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫', 
          icon: 'none' 
        })
      }

      try {
        uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...', mask: true })
        
        const { data: res } = await uni.$http.post('/users/updateProfile', {
          openid: this.userBase.openid,
          nickname: this.editForm.nickname.trim(),
          avatarUrl: this.editForm.avatarUrl,
          user_sex: this.editForm.user_sex,
          major: this.editForm.major.trim(),
          user_introduce: this.editForm.user_introduce.trim()
        })

        if (res.meta.status === 200) {
          // Êõ¥Êñ∞ Vuex
          this.updateUserBase({
            ...this.userBase,
            nickname: this.editForm.nickname.trim(),
            avatarUrl: this.editForm.avatarUrl,
            user_sex: this.editForm.user_sex,
            major: this.editForm.major,
            user_introduce: this.editForm.user_introduce.trim()
          })

          this.hasChanges = false
          
          uni.showToast({ 
            title: '‰øùÂ≠òÊàêÂäü', 
            icon: 'success',
            duration: 1500
          })
          
          setTimeout(() => {
            uni.navigateBack()
          }, 1500)
        } else {
          throw new Error(res.meta.msg || '‰øùÂ≠òÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('‰øùÂ≠òËµÑÊñôÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: error.message || '‰øùÂ≠òÂ§±Ë¥•', 
          icon: 'none' 
        })
      } finally {
        uni.hideLoading()
      }
    },
    
    // Â§¥ÂÉèÂºπÁ™óÊñπÊ≥ï (Êó†‰øÆÊîπ)
    openAvatarSelector() {
      this.$refs.avatarPopup.open()
    },
    
    closeAvatarPopup() {
      this.$refs.avatarPopup.close()
    },
    
    selectAvatar(url) {
      this.editForm.avatarUrl = url
      this.closeAvatarPopup()
    },
  }
}
</script>

